@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
}

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.main-header {
  background-color: transparent;
  position: relative;
}

.main-header__wrapper {
  display: flex;
  padding: 22px 33px;
}
@media screen and (min-width: 768px) {
  .main-header__wrapper {
    padding: 0;
  }
}

.main-header__nav {
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 3;
  display: none;
  left: 0;
  top: 0;
}
@media screen and (min-width: 768px) {
  .main-header__nav {
    position: static;
    display: block;
    background-color: transparent;
  }
}

.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}

.main-header__button {
  display: block;
  background-image: url("../img/stack.svg#menu-close");
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  margin-left: auto;
  z-index: 5;
}
.main-header__button:hover {
  opacity: 0.6;
}
.main-header__button:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-header__button {
    display: none;
  }
}

.no-js .main-header__nav {
  display: block;
  position: static;
  z-index: 5555;
  margin-top: 0;
}

.no-js .main-header__button {
  display: none;
}

.main-header__logo-image {
  max-width: none;
}

main-nav__wrapper {
  padding: 22px 0;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.main-nav__list--desktop {
  display: none;
}

.main-nav__item {
  position: relative;
  padding: 26px 26px 26px 76px;
  text-align: left;
  background-color: var(#ffffff);
  box-shadow: 0px 1px 0px 0px #ececec, 0px 1px 0px 0px #ECECEC inset;
}

.main-nav__item-link {
  color: #000;
  font-family: inherit;
  font-size: 17px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-decoration: none;
}